import React , {useRef, useEffect, useState} from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import Sidebar from '../components/Sidebar';

import "../sass/artist/_arjit.scss";
    
const Artist = ()=>{

    const [searchTerm, setSearchTerm] = useState('');
    const image_demo = useRef();

    const [songs, setSongs] = useState([]);
    useEffect(()=>{
        getSongs();
        return;
    }, []);

    const getSongs = async() =>{
        const config = {
            headers:{
                "Content-Type": "multipart/form-data",
                "Authorization": 1
            }
        }
        try{
            const res = await axios.get('http://localhost:8000/arjit/', config);
            const populate  = setSongs(res.data);
            if(populate){
                return 200;
            }
        }catch(err){
            console.log(err);
        }
    }

    
    const [masterAudio, setMasterAudio] = useState('');
    const [currentIndex, setCurrentIndex] = useState(0);

    const [currentSongIndex, setCurrentSongIndex] = useState(0);
    const [nextSongIndex, setNextSongIndex] = useState(0);

    useEffect(() => {
        setNextSongIndex(() => {
        if (currentSongIndex + 1 > songs.length - 1) {
            return 0;
        } else {
            return currentSongIndex + 1;
        }
        });
    }, [currentSongIndex]);
    console.log(currentSongIndex);


    return(
        <>
            <div className="container-fluid p-0 m-0 whole-cont">
                <div className="row p-0 m-0">
                    <div className="col-xs-6 col-sm-6 col-md-2 col-lg-2 col-xl-2 p-0 m-0 sidecont">
                        <Sidebar/>
                    </div>
                    <div className="col-xs-6 col-sm-6 col-md-10 col-lg-10 col-xl-10 p-0 m-0 mainsectioncontainer">
                        <div className="p-0 m-0 col-sm-12">
                            <div className=" row my-4 mx-4 d-flex">
                                <div className="p-0 m-0 col-sm-12 col-md-10 col-lg-10 col-xl-10">
                                    {/* SearchBar  */}
                                    <div className='d-flex border border-primary' style={{width:'80%', borderRadius: '17px'}}>
                                        <i class="fas fa-search my-auto pl-4" />
                                        <input className="form-control" style={{width: "80%", outline: "none", border: 'none'}} placeholder="Search" classtype="text" name="Search" id="search" onChange={e =>{setSearchTerm(e.target.value )}} />
                                    </div>

                                    <div className="container" style={{position:"relative"}}>
                                        <ul type="none" className="bg-white container border" style={{zIndex: "2", position: "absolute", width: "75%", outline: "none", border: 'none'}}>
                                            {songs.filter((val)=>{
                                                if(searchTerm == ""){
                                                    return ''
                                                }else if(val.title.toLowerCase().includes(searchTerm.toLowerCase())){
                                                    return val
                                                }
                                            }).map((val, id)=>{
                                                return(
                                                <li onClick={()=>{
                                                    setCurrentSongIndex(id)
                                                }}>{val.title}</li>
                                                );
                                            })}
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
    
                        <div className="container-fluid row artist-div">
                            <div className="col-sm-12 col-md-4 col-lg-3">
                                <Link to ="/arrahman">
                                    <img className="img-fluid rounded-circle border border" src="https://snoidcdnems05.cdnsrv.jio.com/c.saavncdn.com/artists/AR_Rahman_002_20210120084455_500x500.jpg" alt="Image Not Found"/>
                                    <h6 className="text-center muted p-2">A.R.Rahman</h6>
                                </Link>
                            </div>  
                            <div className="col-sm-12 col-md-4 col-lg-3">
                                <Link to="/arjit">           
                                    <img className="img-fluid rounded-circle border" src="https://sklktcdnems01.cdnsrv.jio.com/c.saavncdn.com/artists/Arijit_Singh_500x500.jpg" alt="Image Not Found"/>
                                    <h6 className="text-center muted p-2">Arijit Singh</h6>
                                </Link>
                            </div>  
                            <div className="col-sm-12 col-md-4 col-lg-3"> 
                                <Link to="/shreyaghoshal">
                                    <img className="img-fluid rounded-circle border" src="https://sklktcdnems07.cdnsrv.jio.com/c.saavncdn.com/artists/Shreya_Ghoshal_002_20200822043816_500x500.jpg" alt="Image Not Found"/>
                                    <h6 className="text-center muted p-2">Shreya Ghoshal</h6>
                                </Link>
                            </div>
                            <div className="col-sm-12 col-md-4 col-lg-3">  
                                <Link to="/sonunigam">
                                    <img className="img-fluid rounded-circle border" src="https://sdlhivkecdnems05.cdnsrv.jio.com/c.saavncdn.com/artists/Sonu_Nigam_500x500.jpg" alt="Image Not Found"/>
                                    <h6 className="text-center muted p-2">Sonu Nigam</h6>
                                </Link>
                            </div>   
                            <div className="col-sm-12 col-md-4 col-lg-3">
                                <Link to="/latamangeshkar">           
                                    <img className="img-fluid rounded-circle" src="https://sklktcdnems04.cdnsrv.jio.com/c.saavncdn.com/artists/Lata_Mangeshkar_002_20200212082631_500x500.jpg" alt="Image Not Found"/>
                                    <h6 className="text-center text-muted muted p-2">Lata Mangeshkar</h6>
                                </Link>
                            </div>
                            <div className="col-sm-12 col-md-4 col-lg-3">    
                                <Link to="/edsheeran">
                                    <img className="img-fluid rounded-circle border" src="https://snoidcdnems03.cdnsrv.jio.com/c.saavncdn.com/artists/Ed_Sheeran_500x500.jpg" alt="Image Not Found"/>
                                    <h6 className="text-center text-muted muted p-2">Ed Sheeran</h6>
                                </Link>
                            </div>
                            <div className="col-sm-12 col-md-4 col-lg-3">
                                <Link to="/justin">
                                    <img className="img-fluid rounded-circle border" src="https://snoidcdnems02.cdnsrv.jio.com/c.saavncdn.com/artists/Justin_Timberlake_500x500.jpg" alt="Image Not Found"/>
                                    <h6 className="text-center text-muted muted p-2">Justin Timberlake</h6>
                                </Link>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Artist;